
* {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#navbar {
  background-color: rgb(62, 168, 237);
  max-width: 100%;
  height: 70px;
}
.main-logo {
  float: left;
  color: white;
  font-size: 25px;
  padding: 15px;
}
.showing-nav ul {
  list-style-type: none;
}
.showing-nav ul li{
  float: right;
  padding: 20px;
  text-decoration:none
}
.links {
  text-decoration: none;
  color: white;
  font-size: 15;
}
div.text-area {
  margin-top: 15%;
}
h1 {
  padding:20px;
}
div.info-box {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin: 20px;

}
 .box1,.box2,.box3 {
  border: 1px  solid white;
  width: 300px;
  height: 200px;
  margin: 30px;
  text-align: center;
  padding: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
 p{
  color: rgb(62, 168, 237);
  font-size: 25px;
  padding: 10px;
}
span {
  margin-bottom: 20px; 
}
.special-information {
  margin-top: 20px;
  font-size: 20px;
}
div.topic {
  text-align: center;
  font-size: 30px;
  color: grey;
}
div.all-services {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin: 20px;
  font-size: 20px;
  color: grey;
}
li {
  padding: 10px;
}
.input-Entrys {
  text-align: center;
  color: rgb(62, 168, 237);
  margin: 10px;
}
.input-field {
  margin: 20px;
