  left:20px;
  font-size: 20px;
  border:none;
  border-radius:5%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12), 0 6px 20px 0 rgba(0, 0, 0, 0.12);
}
input[type="email"]{
  width: 400px;
  height: 50px;
  text-align: left;
  font-size: 20px;
  left:20px;
  border:none;
  border-radius:5%;
